.navBar {
    position: fixed;
    top: 0;
    z-index: 3;
    min-height: 65px;
    width: 100vw;
    transition: top 0.3s;
    display: flex;
    align-items: center;
}

.navbar-hidden {
    top: -65px;
}

.navBar > h2 {
	width: 100%;
	text-align: right;
	padding-right: 20px;
}
.navBar>h2>* {
	text-decoration: none;
}
